<template>
  <div class="black-bg" v-if="isOpen">
    <div class="white-bg">
      <h4>{{ oneroom.title }}</h4>
      <img :src="oneroom.image" class="room-img" />
      <p>{{ oneroom.content }}</p>
      <input v-model="month" />
      개월
      <p>{{ month }}개월 총 가격 {{ oneroom.price * month }}원</p>
      <button
        @click="
          $emit('closeModal');
          month = 1;
        "
      >
        닫기
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "ModalComponent",
  data() {
    return {
      month: 1,
    };
  },
  watch: {
    month(m, prev) {
      var msg;
      if (m > 12) {
        msg = "최대 개월수는 12개월 입니다.";
      } else if (m < 1) {
        msg = "최소 개월수는 0개월 입니다.";
      } else if (isNaN(m) || /\s/.test(m)) {
        msg = "개월수는 숫자만 가능합니다.";
      }

      if (msg != null) {
        alert(msg);
        this.month = prev;
      }
    },
  },
  beforeUpdate() {
    if(this.month == 2) {
      alert("개월수는 3개월부터 가능합니다.");
      this.month = 3;
    }
  },
  props: {
    oneroom: Object,
    isOpen: Boolean,
  },
};
</script>

