<template>
  <transition name="fade">
    <Modal
      @closeModal="isOpen = false"
      :oneroom="onerooms[curNum]"
      :isOpen="isOpen"
    />
  </transition>

  <div class="menu">
    <a v-for="menu in menus" :key="menu">{{ menu }}</a>
  </div>

  <Discount v-if="showDiscount" :discountAmount="discountAmount" />

  <button @click="nameSortInc()">이름순정렬</button>
  <button @click="priceSortInc()">가격순정렬</button>
  <button @click="priceSortDec()">가격역순정렬</button>
  <button @click="sortBack()">정렬 취소</button>

  <Card
    @openModal="openModal($event)"
    v-for="oneroom in onerooms"
    :key="oneroom"
    :oneroom="oneroom"
  />
</template>

<script>
import onerooms from "./assets/oneroom.js";
import Discount from "./components/DiscountComponent.vue";
import Modal from "./components/ModalComponent.vue";
import Card from "./components/CardComponent.vue";

export default {
  name: "App",
  data() {
    return {
      discountAmount: 30,
      showDiscount: true,
      menus: ["Home", "Shop", "About"],
      initOnerooms: [...onerooms],
      onerooms,
      isOpen: false,
      curNum: 0,
    };
  },

  methods: {
    increase(i) {
      this.reports[i]++;
    },
    openModal(i) {
      this.isOpen = true;
      this.curNum = i;
    },
    nameSortInc() {
      this.onerooms.sort((a, b) => a.title.localeCompare(b.title));
    },
    priceSortInc() {
      this.onerooms.sort((a, b) => a.price - b.price);
    },
    priceSortDec() {
      this.onerooms.sort((a, b) => b.price - a.price);
    },
    sortBack() {
      this.onerooms = [...this.initOnerooms];
    },
  },
  mounted() {
    let decDiscount = setInterval(() => {
      console.log(this.discountAmount);
      if (this.discountAmount > 0) {
        this.discountAmount -= 10;
      } else {
        this.showDiscount = false;
        clearInterval(decDiscount);
      }
    }, 1000);
  },

  // ajax를 이용한 데이터는 created 나 mounted 에서 가져온다.
  components: {
    Discount,
    Modal,
    Card,
  },
};
</script>

